// =============================================================================
// > MODULE : INTERACTIVE - SLICK
// =============================================================================

@import "assets/vendors/slick/slick";
// @import "assets/vendors/slick/slick-theme"

.slick-slider {
    // border: 4px solid white
    // outline: 1px solid darken(color(light-grey), 2%)
    box-shadow: 15px 15px 50px rgba(black, .2);

    & > li {
        display: none; }

    & > li:first-child {
        display: block; }

    img {
        display: block;
        margin: 0; }

    // ==================================================
    // > ARROWS
    // ==================================================
    .slick-arrow {
        appearance: none;
        @include size(40px);
        @include absolute(top 50%);
        background: color(primary);
        text-indent: -9990em;
        border: 0;
        border-radius: 50%;
        margin-top: -20px;
        z-index: 1000;
        cursor: pointer;
        transition: all .25s ease;
        &::before {
            @include font(20px 40px fa white center);
            @extend %cover;
            text-indent: 0; }
        &:hover {
            background: color(secondary); }
        &.slick-prev {
            left: -20px;
            &::before {
                content: $fa-var-angle-left; } }
        &.slick-next {
            right: -20px;
            &::before {
                content: $fa-var-angle-right; } }

        @include print {
            display: none !important; } }


    // ==================================================
    // > DOTS
    // ==================================================
    .slick-dots {
        @include size(100%, 14px);
        @include flex(row nowrap, flex-end, center);
        @include absolute(top -20px right 0);
        li {
            @include size(12px);
            border: 1px solid #cccccc;
            border-radius: 50%;
            margin: 3px;
            cursor: pointer;
            transition: all .25s ease;
            button {
                display: none; }
            &.slick-active {
                background: #cccccc; }
            &:hover {
                border-color: color(secondary);
                background: color(secondary); } }
        @include print {
            display: none !important; } } }



