// =============================================================================
// > LAYOUTS: SITE HEADER
// =============================================================================

.site-header {

    padding: 30px 0;
    line-height: 50px;

    // ==================================================
    // > BRAND
    // ==================================================
    &__brand {
        display: block;
        height: 50px;
        width: 50px;
        text-indent: -9999em;
        background: img("logo.png") center center / contain no-repeat; }


    // ==================================================
    // > MAIN MENU
    // ==================================================
    &__menu {
        @include flex(row nowrap, space-around, center);
        position: relative;
        z-index: 10;
        li {
            position: relative;
            z-index: 10; }
        a {
            @include font(18px 38px text);
            padding: 0 10px;
            &::after {
                content: none !important; }
            &:hover {
                @include font(primary); } }

        &__indicator {
            @include absolute(bottom 0 left 0);
            @include size(0, 5px);
            background: color(primary);
            transition: all .5s easing(bounce); } } }



