// =============================================================================
// > UTILS - SHORTCUTS
// =============================================================================

// ==================================================
// > FONTS
// > Ex: +font(12px 20px primary bold center uppercase)
// > Can be used in any order, but prefer the following for consistency :
// > - font size
// > - line height
// > - color
// > - font stack
// > - text align
// > - text transform
// ==================================================
@mixin font($args) {
    $hasFontSize: false;

    @each $a in $args {

        @if type-of($a) == "number" {
            // Font size
            @if not $hasFontSize {
                @if unitless($a) { // use px as default unit
                    $a: $a * 1px; }
                font-size: $a;
                font-size: calculateRem($a);
                $hasFontSize: true; }

            // Line height
            @else {
                line-height: $a; } }


        // Font color
        @if map-has-key($colors, $a) {
            color: color($a); }
        @elseif type-of($a) == "color" {
            color: $a; }

        // Font family
        @if map-has-key($fonts-stacks, $a) {
            font-family: font($a); }

        // Text align
        @each $ta in left right center {
            @if $a == $ta {
                text-align: $ta; } }

        // Text transform
        @each $tt in uppercase lowercase capitalize {
            @if $a == $tt {
                text-transform: $a; } } } }


// ==================================================
// > POSITIONS
// ==================================================
@mixin pos($position, $args) {
    position: $position;
    @each $o in top right bottom left {
        $i: index($args, $o);
        @if $i and $i + 1 <= length($args) and type-of(nth($args, $i + 1)) == number {
            #{$o}: nth($args, $i + 1); } } }

@mixin absolute($args: "") {
    @include pos(absolute, $args); }

@mixin fixed($args: "") {
    @include pos(fixed, $args); }

@mixin relative($args: "") {
    @include pos(relative, $args); }


// ==================================================
// > SIZES
// ==================================================
@mixin size($width, $height: $width) {
    width: $width;
    height: $height; }


// ==================================================
// > FLEX
// ==================================================
@mixin flex($flow, $jc: null, $ai: null) {
    display: flex;
    flex-flow: $flow;
    justify-content: $jc;
    align-items: $ai; }



// ==================================================
// > PLACEHOLDERS
// ==================================================
@mixin placeholder {
    &.placeholder {
        @content; }
    &:-moz-placeholder {
        @content; }
    &::-moz-placeholder {
        @content; }
    &:-ms-input-placeholder {
        @content; }
    &::-webkit-input-placeholder {
        @content; } }


// ==================================================
// > TRUNCATE
// ==================================================
@mixin truncate($truncation-boundary) {
    max-width: $truncation-boundary;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }



