// =============================================================================
// > TYPOGRAPHY - TITLES
// =============================================================================

h1, h2, h3, h4, h5, h6 {
    font-weight: normal;
    text-rendering: geometricPrecision;
    margin: 1.5em 0 1em; }

h1 {
    @include font(40px bold); }

h2 {
    @include font(35px bold); }

h3 {
    @include font(30px bold); }

h4 {
    @include font(24px bold); }

h5 {
    @include font(20px bold); }

h6 {
    @include font(16px bold); }



