// =============================================================================
// > UTILS - MEDIA QUERIES
// =============================================================================

// ==================================================
// > SCREEN
// > Usage :
// > +screen(md)
// >    color: red
// ==================================================

@mixin screen($bp, $compare: "-", $dim: "width") {

    @if map-has-key($responsive, $bp) {
        $bp: breakpoint($bp); }

    // Max width
    @if $compare == "-" {
        @media screen and (max-#{$dim}: $bp) {
            @content; } }

    // Min width
    @else if $compare == "+" {
        @media screen and (min-#{$dim}: $bp) {
            @content; } }

    // Between
    @else {
        @if map-has-key($responsive, $compare) {
            $compare: breakpoint($compare); }

        @media screen and (min-#{$dim}: $bp) and (max-#{$dim}: $compare) {
            @content; } } }





// ==================================================
// > PRINT
// ==================================================
@mixin print {
    @media print {
        @content; } }



