// =============================================================================
// > MODULES - LISTS: ARCHIVE
// =============================================================================

.archive {

    // ==================================================
    // > LIST
    // ==================================================
    &__list {
        margin-left: -15px;
        margin-right: -15px;
        @include flex(row wrap); }


    // ==================================================
    // > ITEM
    // ==================================================
    &__item {
        padding-left: 15px;
        padding-right: 15px;
        width: 50%; }


    // ==================================================
    // > NO RESULTS
    // ==================================================
    &__noresults {
        @include font(20px 80px bold #ccc center uppercase); } }



