// =============================================================================
// > BASE : @extend CLASSES.
// =============================================================================

// ==================================================
// > CLEARFIX
// ==================================================
%clearfix {
    &::after {
        content: "";
        display: table;
        clear: both; } }


// ==================================================
// > UNSTYLED LIST
// ==================================================
%unstyled-list {
    list-style: none;
    &, li {
        margin: 0;
        padding: 0;
        &::before {
            content: none; } } }

// ==================================================
// > CENTERED ITEM
// ==================================================
%centered {
    @include absolute(left 50% top 50%);
    transform: translate(-50%, -50%); }


// ==================================================
// > COVER PARENT
// ==================================================
%cover {
    @include absolute(left 0 top 0);
    @include size(100%); }



