// =============================================================================
// > LAYOUTS: TEMPLATES - SINGLE
// =============================================================================

.single {

    // ==================================================
    // > NAV
    // ==================================================
    &__nav {
        padding: 10px 0;

        &--top {
            border-bottom: 1px solid color(medium-grey); }
        &--bottom {
            border-top: 1px solid color(medium-grey); }

        &__archive {
            @include font(16px); }

        &__prev,
        &__next {
            float: right;
            @include font(20px 44px medium black);
            transition: all .25s ease;
            &::before {
                content: $fa-var-angle-right;
                @include font(20px 30px center fa primary);
                @include size(30px);
                border: 1px solid color(primary);
                border-radius: 50%;
                margin: 3px 5px;
                float: left;
                transition: all .25s ease; }
            &:hover {
                @include font(primary);
                &::before {
                    background: color(primary);
                    color: color(white); } } }

        &__prev::before {
            content: $fa-var-angle-left;
            float: right; } }


    // ==================================================
    // > CONTENT
    // ==================================================
    &__content {
        padding: 40px 0; }

    // =============================================================================
    // > TYPES
    // =============================================================================

    /* NEWS */
 }    // &-news &



