// =============================================================================
// > MODULE - INTERACTIVE : BUTTON
// =============================================================================
%button {
    @include font(16px 1.2 center white);
    position: relative;
    display: inline-flex;
    z-index: 5;
    border: 1px solid color(primary);
    cursor: pointer;
    padding: 10px 20px;
    transition: all .35s ease;

    &::before {
        content: "";
        @include size(100%);
        background: color(primary);
        @include absolute(top 0 left 0);
        z-index: -1;
        transition: width .35s ease; }

    &:hover {
        @include font(primary);
        &::before {
            width: 0;
            left: 100%;
            transition: all .35s ease; } } }

.button {
    @extend %button;

    // ========== MODIFIERS ========== //
    &--fullwidth {
        display: block;
        width: 100%; }

    &--small {
        @include font(13px);
        padding: 5px; }

    &--big {
        @include font(20px bold);
        padding: 20px; }

    &--white {
        border-color: white;
        @include font(primary);
        &::before {
            background: white; }
        &:hover {
            @include font(white); } }

    &--ghost {
        @include font(primary);
        &::before {
            width: 0;
            left: 100%;
            transition: width .35s ease, left 0s ease .35s; }
        &:hover {
            @include font(white);
            &::before {
                width: 100%;
                left: 0; } }

        &.button--white {
            @include font(white);
            &:hover {
                @include font(primary); } } } }



