// =============================================================================
// > LAYOUTS: TEMPLATES - SEARCH
// =============================================================================

.search-page {

    &__title {
        @include font(text);
        margin-bottom: 60px;
        &__words {
            @include font(primary); }
        small {
            @include font(light); } }

    &__type {
        margin: 0;
        @include font(20px left secondary uppercase); }

    // ==================================================
    // > RESULTS
    // ==================================================
    &__results {
        padding: 60px 0; }

    &--no-results &__results {
        display: none; } }



