// =============================================================================
// > LAYOUTS: MODULES - FORMS
// =============================================================================

$input-height: 50px;
$input-background: white;
$input-border-radius: 0;
$input-border: 1px solid #e6e6e6;
$input-padding: 18px;
$input-color: text;

// ==================================================
// > FORM
// ==================================================
form {
    hr {
        margin: 50px 0;
        height: 0;
        margin-bottom: 1px solid color(secondary); }

    .hidden-wrap {
        display: none; } }

// ==================================================
// > FIELDSET
// ==================================================
fieldset {
    margin-bottom: spacing(sm); }

// ==================================================
// > LABEL
// ==================================================
label {
    @include font(18px light);
    cursor: pointer; }

// ==================================================
// > PLACEHOLDERS
// ==================================================
%input {
    @include placeholder {
        @include font(light rgba(color(text), .3)); } }

// ==================================================
// > INPUT
// ==================================================
%input {
    @include size(100%, $input-height);
    @include font(16px $input-height $input-color);
    background: $input-background;
    border: $input-border;
    padding: 0 $input-padding;
    border-radius: $input-border-radius;
    resize: none;
    &.no-border {
        border: 0; } }

input {
    @extend %input; }

// ==================================================
// > TEXTAREA
// ==================================================
textarea {
    @extend %input;
    min-height: 200px;
    padding: 10px $input-padding;
    line-height: 1.2;
    display: block;
    margin: 0; }


// ==================================================
// > RADIO / CHECKBOX
// ==================================================
input[type="radio"],
input[type="checkbox"] {
    @include size(14px);
    appearance: none;
    position: relative;
    margin: 8px 5px 0;
    padding: 0;
    border: 0;

    &::before {
        content: $fa-var-square-o;
        @include font(16px 20px primary fa);
        @include absolute(top 0 left 0);
        height: 14px; }

    &:checked::before {
        content: $fa-var-check-square-o; } }

input[type="radio"] {
    border: 2px solid color(primary);
    border-radius: 50%;
    &::before {
        content: "";
        @include size(6px);
        @include absolute(top 50% left 50%);
        opacity: 0;
        background: color(primary);
        border-radius: 50%;
        margin-top: -3px;
        margin-left: -3px; }
    &:checked::before {
        content: "";
        opacity: 1; } }


// ==================================================
// > SUBMIT
// ==================================================
input[type="submit"],
input[type="button"] {
    @extend %button;
    @include font(16px $input-height white regular center uppercase);
    padding: 0 $input-padding;
    height: $input-height;
    margin-top: 27px;
    cursor: pointer;
    clear: both;
    display: block;
    background: color(primary);
    border: 2px solid color(primary);
    &:hover {
        background: none;
        color: color(primary); }

    .loading & {
        opacity: .5;
        pointer-events: none; } }

